body{
  background: #eee;
}
#root{
  display: flex;
  justify-content: center;
  min-height: 100vh;
}
body{
    text-align: center;
}
.App{
    border: 1px solid #ddd;
    color: #333;
    background: #fff;
    margin: 2em;
    padding: 0 1em;
    min-height: 20em;
    min-width: 32em;
    box-shadow: 0 0 1em hsla(0,0%,0%,0.15);
}
.App ol li{
    margin: 10px;
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
*::after,*::before{
    box-sizing: border-box;
}
ul,ol,dl{
    list-style: none;
}
a{
    color:inherit;
}

